export default {
    data(){
        return{
            name:'',
            username:'',
            userpwd:'',
            required:true,
            reg_url: this.HOST.host + '/register',
        }
    },
    methods:{
        register:function(){
            let _self = this;
            if(this.username !== '' && this.userpwd !== ''){
                this.$http.post(this.reg_url,{
                    // name:this.name,
                    username:this.username,
                    userpassword:this.userpwd
                })
                .then(res => {
                    // console.log(res)
                    if(res.data.code === 200){
                        this.$vux.alert.show({
                            title: '',
                            content: '()',
                            onShow () {
                            },
                            onHide () {
                                _self.$router.push('/login')
                            }
                        })
                    }
                    else if(res.data.code === 404){
                        this.$vux.toast.show({
                            text: '',
                            type:'warn'
                        })
                    }
                }), res => {  
                    console.log("");  
                    console.log(res);  
                }            
            }
            else{
                this.$vux.toast.show({
                    text: '',
                    type:'cancel'
                })
            }
            
        }
    }
}



